import flow from 'lodash/flow';
import React, { Component, PropTypes } from 'react';
import { DragSource, DropTarget } from 'react-dnd';
import ItemTypes from './ItemTypes';
import { BUILDING_COSTS, COSTS, HEADERS } from './BuildingConsts';


const buildingSource = {
  beginDrag(props) {
    return {
      id: props.id,
    };
  },

  endDrag(props, monitor) {
    const { id: droppedId } = monitor.getItem();
    const didDrop = monitor.didDrop();

    if (!didDrop) {
      props.removeBuilding(droppedId);
    }
  },
};

const buildingTarget = {
  canDrop() {
    // Don't handle the drop here, let the outer container recognize it
    return false;
  },

  // Called for the object being hovered over
  hover(props, monitor) {
    // Find the id of the object being dragged
    const { id: draggedId } = monitor.getItem();
    const { id: overId } = props;

    if (draggedId !== overId) {
      const { index: overIndex } = props.findBuilding(overId);
      props.moveBuilding(draggedId, overIndex);
    }
  },
};


class BuildingPriceRow extends Component {
    static propTypes = {
        connectDragSource: PropTypes.func.isRequired,
        connectDropTarget: PropTypes.func.isRequired,
        isDragging: PropTypes.bool.isRequired,

        moveBuilding: PropTypes.func.isRequired,
        removeBuilding: PropTypes.func.isRequired,
        findBuilding: PropTypes.func.isRequired,

        id: PropTypes.number.isRequired,
        rank: PropTypes.number.isRequired,
        text: PropTypes.string.isRequired,
        coffer: PropTypes.object.isRequired,
        partialCosts: PropTypes.object.isRequired
    };

    render() {
        const { partialCosts, id, rank, text, coffer, alianceDiscount } = this.props;
        const { isDragging, connectDragSource, connectDropTarget } = this.props;
        const opacity = isDragging ? 0.5 : 1;

        return connectDragSource(connectDropTarget(
            <tr style={{ opacity }}>
            {HEADERS.map(header => {
                let style;
                let primary;
                let secondary;

                if (COSTS.includes(header)){
                    primary = (BUILDING_COSTS[text][rank-1][header] * alianceDiscount).toFixed(2)
                    if (partialCosts[header] <= coffer[header]) {
                        style = "success";
                    } else if (BUILDING_COSTS[text][rank-1][header]) {
                        const costDifference = (partialCosts[header] || 0) - (coffer[header] || 0)
                        secondary = (
                            <small className="text-danger">
                                ({costDifference.toFixed(2)})
                            </small>
                        );
                    }
                } else {
                    primary = BUILDING_COSTS[text][rank-1][header];
                }
                return (
                    <td key={header}
                        className={style}>
                        {primary}
                        {secondary}
                    </td>
                );
            })}
            </tr>
        ));
    }
}


export default flow(
  DragSource(ItemTypes.BUILDING, buildingSource, (connect, monitor) => ({
  connectDragSource: connect.dragSource(),
  isDragging: monitor.isDragging(),
})),
  DropTarget(ItemTypes.BUILDING, buildingTarget, connect => ({
  connectDropTarget: connect.dropTarget(),
}))
)(BuildingPriceRow);



