import flow from 'lodash/flow';
import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { DropTarget, DragDropContext } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';
import ItemTypes from './ItemTypes';
import {BUILDING_TYPES, BUILDING_GROUPS } from './BuildingConsts';


const style = {
  // border: '1px dashed gray',
  // padding: '0.5rem 1rem',
  // marginBottom: '.5rem',
  // backgroundColor: 'white',
  // cursor: 'move',
};

function addBuilding(text) {
    return {
        type: 'ADD_BUILDING',
        text: text
    }
}


class MasterBuilding extends Component {
    onClick() {
        const { clickDispatch, text } = this.props;
        clickDispatch(text);
    }

    render() {
        const { text, rank } = this.props;
        return(
            <button type="button" className="btn btn-default" onClick={this.onClick.bind(this)}>
                {text} - {rank || 0}
            </button>
            )
    }
}

class MasterBuildingGroup extends Component {

    render() {
        const { names, alreadyBuilt, clickDispatch } = this.props;
        return (
            <div>
            {names.map((name) =>(
                <div className="row" key={name}>
                    <div className="col-xs-12">
                        <MasterBuilding
                          text={name}
                          rank={alreadyBuilt[name]}
                          clickDispatch={clickDispatch}
                        />
                    </div>
                </div>
            ))}
            </div>
        )
    }
}

class BuildingSource extends Component {
    render() {
      const { clickDispatch, alreadyBuilt } = this.props;

      return(
        <div>
          {BUILDING_TYPES.map((building) => {
            return <MasterBuilding
              key={building}
              text={building}
              rank={alreadyBuilt[building]}
              clickDispatch={clickDispatch}
            />
          })}
        </div>
        )
    }

    render() {
        const { clickDispatch, alreadyBuilt } = this.props

        return (
            <div className="row">
                <div className="col-xs-3">
                    <div className="panel panel-primary">
                      <div className="panel-heading">
                        <h3 className="panel-title">Primary</h3>
                      </div>
                      <div className="panel-body">
                        <MasterBuildingGroup alreadyBuilt={alreadyBuilt}
                                             names={BUILDING_GROUPS['PRIMARY']}
                                             clickDispatch={clickDispatch}/>
                      </div>
                    </div>
                </div>
                <div className="col-xs-3">
                    <div className="panel panel-primary">
                      <div className="panel-heading">
                        <h3 className="panel-title">Boons</h3>
                      </div>
                      <div className="panel-body">
                        <MasterBuildingGroup alreadyBuilt={alreadyBuilt}
                                             names={BUILDING_GROUPS['BOONS']}
                                             clickDispatch={clickDispatch}/>
                      </div>
                    </div>
                </div>
                <div className="col-xs-3">
                    <div className="panel panel-primary">
                      <div className="panel-heading">
                        <h3 className="panel-title">Boons</h3>
                      </div>
                      <div className="panel-body">
                        <MasterBuildingGroup alreadyBuilt={alreadyBuilt}
                                             names={BUILDING_GROUPS['PRODUCTION']}
                                             clickDispatch={clickDispatch}/>
                      </div>
                    </div>
                </div>
                <div className="col-xs-3">
                    <div className="panel panel-primary">
                      <div className="panel-heading">
                        <h3 className="panel-title">Support</h3>
                      </div>
                      <div className="panel-body">
                        <MasterBuildingGroup alreadyBuilt={alreadyBuilt}
                                             names={BUILDING_GROUPS['SUPPORT']}
                                             clickDispatch={clickDispatch}/>
                      </div>
                    </div>
                </div>
            </div>
        )

    }
}

function mapStateToProps(state) {
    return {
        alreadyBuilt: state.alreadyBuilt
    }
}

function mapDispatchToProps(dispatch) {
    return {
        clickDispatch: (text) =>{
            dispatch(addBuilding(text));
        }
    }
}

BuildingSource = connect(
    mapStateToProps,
    mapDispatchToProps
)(BuildingSource)

export default BuildingSource



