import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import BuildingPriceContainer from './BuildingPriceContainer';
import BuildingSource from './BuildingSource';
import StrongholdConfig from './StrongholdConfig';

class App extends Component {
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="bg-primary col-xs-12">
            <img src={logo} className="App-logo" alt="logo" />
            <h1>Welcome to Stronghold Calcutron</h1>
          </div>
        </div>
        <br />
        <div className="row">
          <div className="col-xs-12">
            <BuildingSource />
          </div>
        </div>
        <div className="row">
          <div className="col-xs-12">
            <BuildingPriceContainer />
          </div>
        </div>
        <div className="row">
          <div className="col-xs-12">
            <StrongholdConfig />
          </div>
        </div>
      </div>
    );
  }
}

export default App;



