// export default todos



const defaultState = {
  alreadyBuilt: {
    'Barracks': 5,
    'Farm': 5,
    'Guild Hall': 12,
    'Lumberyard': 5,
    'Marketplace': 5,
    'Mine': 5,
    'Quarry': 5,
    'Second Warehouse': 2,
    'Stable': 5,
    'Temple': 0,
    'Warehouse': 5,
  },
  buildings: [],
  coffer: {
    "AD": "21.658",
    "ADVENTURE": "56699",
    "DREAD RING": "925.818",
    "DUNGEON": "30940",
    "GEMS": "449.873",
    "GLORY": "489.080",
    "GOLD": "1100",
    "HERO": "21389",
    "ICEWIND": "894.873",
    "INF": "537.662",
    "LABOR": "392.700",
    "PVP Shards": "15690",
    "SHARANDAR": "982.869",
    "SURPLUS": "907.498",
    "TYRANNY": "3781.060"
  },
  alianceDiscount: 0.9365,
  nextId: 1
}

function updateRanks(alreadyBuilt, newBuildings) {
  let newRanks = {
    ...alreadyBuilt
  };
  newBuildings = newBuildings.map(building => {
    const rank = (newRanks[building.text] || 0) + 1
    newRanks[building.text] = rank;
    return {
      ...building,
      rank: rank
    }
  })
  return newBuildings
}

function validateBuildings(buildings) {
    return buildings.filter((building) =>
        building.rank <= BUILDING_COSTS[building.text].length
      )
}

function syncBuildings(alreadyBuilt, buildings) {
  return validateBuildings(updateRanks(alreadyBuilt, buildings))
}


const strongholds = (state = defaultState, action) => {
  let newBuildings = {};
  switch (action.type) {
    case 'ADD_BUILDING':
      const newBuilding = {
          id: state.nextId,
          text: action.text,
          rank: 0
      }
      newBuildings = state.buildings.slice();
      newBuildings.push(newBuilding);

      newBuildings = syncBuildings(state.alreadyBuilt, newBuildings);

      return {
        ...state,
        buildings: newBuildings,
        nextId: state.nextId + 1
      }

    case 'MOVE_BUILDING':
      const building = state.buildings[action.index];
      newBuildings = state.buildings.slice();

      newBuildings.splice(action.index, 1);
      newBuildings.splice(action.atIndex, 0, building)

      newBuildings = syncBuildings(state.alreadyBuilt, newBuildings);

      return {
        ...state,
        buildings: newBuildings
      }

    case 'REMOVE_BUILDING':
      newBuildings = state.buildings.slice();
      newBuildings.splice(action.index, 1);
      newBuildings = syncBuildings(state.alreadyBuilt, newBuildings);
      return {
        ...state,
        buildings: newBuildings
      }

    case 'UPDATE_COFFER':
      return {
        ...state,
        coffer: {
          ...state.coffer,
          [action.name]: action.value
        }
      }

    case 'UPDATE_BUILDING_LEVEL':
      const newAlreadyBuilt = {
          ...state.alreadyBuilt,
          [action.name]:action.value
        }
      return {
        ...state,
        alreadyBuilt: newAlreadyBuilt,
        buildings: syncBuildings(newAlreadyBuilt, state.buildings)
      }
    default:
      return state
  }
}

export default strongholds



