import flow from 'lodash/flow';
import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { DragSource, DropTarget } from 'react-dnd';
import ItemTypes from './ItemTypes';
import { HEADERS, BUILDING_GROUPS } from './BuildingConsts';


function updateBuildLevel(name, value){
    return {
        type: 'UPDATE_BUILDING_LEVEL',
        name,
        value:parseInt(value)
    }
}


class StrongholdConfigGroup extends Component {
    render() {
        const { names, alreadyBuilt, onLevelChange } = this.props;
        return (
            <form className="form-horizontal">
                {names.map( (name) =>
                    <div className="form-group" key={name}>
                        <label htmlFor={name} className="col-xs-5 control-label">{name}</label>
                        <div className="col-xs-5">
                            <input type="number"
                                   className="form-control"
                                   id={name}
                                   value={alreadyBuilt[name] || 0}
                                   onChange={onLevelChange(name)}  />
                        </div>
                    </div>
                )}
            </form>
        )
    }
}


class StrongholdConfig extends Component {
    static propTypes = {
        alreadyBuilt: PropTypes.object.isRequired,
        doUpdateBuildLevel: PropTypes.func.isRequired,
    }

    constructor(props) {
        super(props);
        this.onLevelChange = this.onLevelChange.bind(this);
    }

    onLevelChange(name){
        return (event) => this.props.doUpdateBuildLevel(name, event.target.value)
    }

    render() {
        const { alreadyBuilt } = this.props

        return (
            <div className="row">
                <div className="col-xs-3">
                    <h3 className="text-center">Primary</h3>
                    <StrongholdConfigGroup alreadyBuilt={alreadyBuilt}
                                           names={BUILDING_GROUPS['PRIMARY']}
                                           onLevelChange={this.onLevelChange}/>
                </div>
                <div className="col-xs-3">
                    <h3 className="text-center">Boons</h3>
                    <StrongholdConfigGroup alreadyBuilt={alreadyBuilt}
                                           names={BUILDING_GROUPS['BOONS']}
                                           onLevelChange={this.onLevelChange} />
                </div>
                <div className="col-xs-3">
                    <h3 className="text-center">Production</h3>
                    <StrongholdConfigGroup alreadyBuilt={alreadyBuilt}
                                           names={BUILDING_GROUPS['PRODUCTION']}
                                           onLevelChange={this.onLevelChange} />
                </div>
                <div className="col-xs-3">
                    <h3 className="text-center">Support</h3>
                    <StrongholdConfigGroup alreadyBuilt={alreadyBuilt}
                                           names={BUILDING_GROUPS['SUPPORT']}
                                           onLevelChange={this.onLevelChange} />
                </div>
            </div>
        )

    }
}


function mapStateToProps(state) {
  return {
    alreadyBuilt: state.alreadyBuilt,
  }
}

function mapDispatchToProps(dispatch) {
    return {
        doUpdateBuildLevel: (name, value) => {
            dispatch(updateBuildLevel(name, value));
        }
    }
}

StrongholdConfig = connect(
  mapStateToProps,
  mapDispatchToProps
)(StrongholdConfig)

export default StrongholdConfig



