import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { DropTarget, DragDropContext } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';
import flow from 'lodash/flow';

import ItemTypes from './ItemTypes';
import { BUILDING_COSTS, COSTS, HEADERS } from './BuildingConsts';
import BuildingPriceRow from './BuildingPriceRow';


function updateCoffer(name, value){
    return {
        type: 'UPDATE_COFFER',
        name,
        value:value
    }
}

function moveABuilding(id, index, atIndex) {
  return {
    type: 'MOVE_BUILDING',
    id,
    index,
    atIndex
  }
}

function removeABuilding(id, index) {
  return {
    type: 'REMOVE_BUILDING',
    id,
    index
  }
}


class CofferControlRow extends Component {
    static propTypes = {
        coffer: PropTypes.object.isRequired,
        onCofferChange: PropTypes.func.isRequired,
    };

    render() {
        const { coffer, onCofferChange } = this.props;
        return <tr>
            {HEADERS.map(header => {
                let input;
                const editCoffer = false;
                if (COSTS.includes(header)) {
                    if (editCoffer) {
                        input = <input type="text" className="input" value={coffer[header]} onChange={onCofferChange(header)}/>
                    } else {
                        input = coffer[header]
                    }
                } else if (header == 'Building') {
                    input = 'Coffer'
                }
                return <td key={header}>{input}</td>
            })}
        </tr>
    }
}


const buildintTarget = {
    drop() {
    },
};

class BuildingPriceContainer extends Component {
    static propTypes = {
        buildings: PropTypes.array.isRequired,
        coffer: PropTypes.object.isRequired,
        connectDropTarget: PropTypes.func.isRequired,
        alianceDiscount: PropTypes.number.isRequired,
    };

    constructor(props) {
        super(props);
        this.onCofferChange = this.onCofferChange.bind(this);
        this.moveBuilding = this.moveBuilding.bind(this);
        this.removeBuilding = this.removeBuilding.bind(this);
        this.findBuilding = this.findBuilding.bind(this);
    }

    moveBuilding(id, atIndex) {
        const { index } = this.findBuilding(id);
        this.props.onBuildingMove(id, index, atIndex);
    }

    removeBuilding(id) {
        const { index } = this.findBuilding(id);
        this.props.onBuildingRemove(id, index);
    }

    findBuilding(id) {
        const { buildings } = this.props;
        const building = buildings.filter(c => c.id === id)[0];

        return {
            building,
            index: buildings.indexOf(building),
        };
    }

    runningTotal(building_rank_cost, old_cost) {
        const { alianceDiscount } = this.props;
        let new_costs = {};
        COSTS.map(key =>
            new_costs[key] = (old_cost[key] || 0) + ((parseInt(building_rank_cost[key]) || 0) * alianceDiscount)
            )
        return new_costs;
    }

    onCofferChange(name) {
        return (event) => this.props.onCofferUpdate(name, event.target.value)
    }

    render() {
        const { connectDropTarget, buildings, coffer, alianceDiscount } = this.props;

        let partialCosts = {};
        return (
            <div className="row">
            <table className="table table-striped table-hover table-condensed">
                <thead>
                <tr>
                    {HEADERS.map(header =>
                        <th key={header}>{header}</th>
                    )}
                </tr>
                </thead>
                {connectDropTarget(
                    <tbody>
                    <CofferControlRow coffer={coffer}
                                      onCofferChange={this.onCofferChange} />

                    {buildings.map((building, index) => {
                        const { id, text, rank } = building;
                        partialCosts = this.runningTotal(BUILDING_COSTS[text][rank-1], partialCosts)
                        return <BuildingPriceRow key={id}
                                                 id={id}
                                                 text={text}
                                                 rank={rank}
                                                 partialCosts={partialCosts}
                                                 coffer={coffer}
                                                 alianceDiscount={alianceDiscount}
                                                 moveBuilding={this.moveBuilding}
                                                 removeBuilding={this.removeBuilding}
                                                 findBuilding={this.findBuilding}/>
                        })}
                    </tbody>
                )}
            </table>
            </div>
        )
    }
}

function mapStateToProps(state) {
  return {
    buildings: state.buildings,
    coffer: state.coffer,
    alianceDiscount: state.alianceDiscount,
    editCoffer: state.editCoffer,
  }
}

function mapDispatchToProps(dispatch) {
    return {
        onCofferUpdate: (name, value) => {
            dispatch(updateCoffer(name, value));
        },
        onBuildingMove: (id, index, atIndex) => {
            dispatch(moveABuilding(id, index, atIndex));
        },
        onBuildingRemove: (id, index) => {
            dispatch(removeABuilding(id, index));
        }
    }
}

BuildingPriceContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(BuildingPriceContainer)


export default flow(
  DropTarget(ItemTypes.BUILDING, buildintTarget, connect => ({
    connectDropTarget: connect.dropTarget(),
  })),
  DragDropContext(HTML5Backend)
)(BuildingPriceContainer)



